const getters = {
  getLagers(state) {
   return state.lagers;
  },
  getFreeProduct(state){
    return state.freeProduct
  },
  getFreeBranchProduct(state){
    return state.freeBranchProduct;
  },
  getProductsBelegNum(state){
    let products = []
      state.productsBelegNum.forEach(element => {
        console.log(element, state.branchId);
        
        if(state.branchId == element.branchId){
          products.push(element)
        }
      });
    return products;
  },
  getGoods(state){
    return state.goods;
  }

};
const mutations = {
  setBranchId(state,payload){
    state.branchId = payload
  },
  setFreeProduct(state,payload){
      state.freeProduct.push(payload)
  },
  setNewItemsForLagerInBranch(state,payload){
    for (let i = 0; i < payload.items.length; i++) {
      state.freeBranchProduct.push({...payload.items[i], branchId : payload.branchId, belegNo: payload.belegNo, isProblem:false})
      
    }
    console.log(state.freeBranchProduct);
    state.freeProduct=[]
    
  //  state.freeBranchProduct.push(paylaod)
  },
  setRemoveBranchItem(state,payload){
    state.freeBranchProduct.splice(payload,1)
  },
  setItemsInLager(state,payload){
    console.log(payload);
    for (let i = 0; i < payload.items.length; i++) {
      state.lagers[payload.index].allGoods =  state.lagers[payload.index].allGoods + Number(payload.items[i].quantity)
     state.lagers[payload.index].data.push(payload.items[i])   
    }
    state.freeBranchProduct=[]
    //  
  },
  setNewProblemItemsForLagerInBranch(state,payload){
    
      state.productsBelegNum.push({ data: payload.items, createdAt:payload.createdAt, isProblem : true , branchId : payload.branchId, belegNo: payload.belegNo})
   
  },
  setGoodsForProductsBelegNum(state,payload){
    
    state.productsBelegNum.push({ data: payload.items, createdAt: payload.createdAt, isProblem : false , branchId : payload.branchId, belegNo: payload.belegNo})

  },
  setNewProduct(state,payload){
    state.goods.push(payload)
  }
 
};

