<template>
  <b-modal size="xl" title="Free Artikel" hide-footer id="getLagerFreeProduct" ref="getLagerFreeProduct">
  {{index}} {{branchId}} {{multipleSelection}}
   <div class="text-right">
               <button @click="sendItemsInLager()" class="btn btn-outline-primary btn-sm">Hinzufügen</button>

   </div>
       <el-table
      class="border shadow p-3" 
      :data="getFreeBranchProduct"
       ref="multipleTable"
    @selection-change="handleSelectionChange"
      style="width: 100%">
          <el-table-column
      type="selection"
      width="55">
    </el-table-column>
      <el-table-column prop="artikelNum" label="Artikel Nr." sortable> </el-table-column>
      <el-table-column prop="productName" label="Artikel"> </el-table-column>
      <el-table-column
        prop="belegNo"
        label="Beleg Nr."
      >
      </el-table-column>
      <el-table-column prop="quantity" label="Menge"> </el-table-column>
      <el-table-column prop="isEdit" label="Edit">
                          <template slot-scope="scope">
                    <i style="cursor:pointer;font-size:24px" @click="removeItem(scope.$index)" class="text-danger fas fa-minus-circle"></i>
                </template> </el-table-column>

       </el-table>
  </b-modal>
</template>

<script>
import { mapGetters } from 'vuex'
export default {
    props:['index','branchId'],    
    computed:{
        ...mapGetters(['getFreeBranchProduct'])
    },
    data(){
        return {
                multipleSelection: []
        }
    },
    methods:{
        removeItem(index)
        {
            this.$store.dispatch('removeFreeBranchItem',index)
        },
        sendItemsInLager(){
            this.$store.dispatch('sendItemsInLager',{items : this.multipleSelection, index: this.index})
                    setTimeout(() => {
            this.$notify({
          title: "Erfolg",
          message: "Das Produkt wurde erfolgreich hinzugefügt.",
          type: "success"
        });
        }, 150);
        this.$refs['getLagerFreeProduct'].hide();
        },
          toggleSelection(rows) {
        if (rows) {
          rows.forEach(row => {
            this.$refs.multipleTable.toggleRowSelection(row);
          });
        } else {
          this.$refs.multipleTable.clearSelection();
        }
      },
      handleSelectionChange(val) {
        this.multipleSelection = val;
      }

    }

}
</script>

